<!-- by 添少 -->
<template>
  <section class="ui-cover">
    <section class="bd">
      <img style="max-width: 100%;" ref="cover">
    </section>
    <section class="ft">
      <button @click="handCancel">取消</button>
      <button @click="handReset">还原</button>
      <button @click="handDone">完成</button>
    </section>
  </section>
</template>
<script>
  import {mapState} from 'vuex'
  import FileImage from '@/components/file-image'

  import 'cropperjs/dist/cropper.min.css'
  import Cropper from 'cropperjs'

  let cropper = null
  export default {
    name: '',
    components: {
      FileImage
    },
    props: {
      src: {
        type: String,
        require: false,
        default: ''
      },
      ar: {
        type: Number,
        require: false,
        default: 1,
      }
    },
    data() {
      return {}
    },
    watch: {
      src(val) {
        cropper && cropper.destroy()
        this.$refs.cover.src = val
        cropper = new Cropper(this.$refs.cover, {
          aspectRatio: this.ar,
          crop(event) {
            // console.log(event.detail)
          },
        })
        // console.log(cropper)
        cropper.reset()
      }
    },
    computed: {},
    created() {
      this.init()
    },
    mounted() {
    },
    methods: {
      init() {
      },
      initData() {
      },
      handCancel() {
        this.$emit('cancel')
      },
      handReset() {
        cropper.reset()
        this.$emit('reset')
      },
      handDone() {
        this.$emit('done', cropper)
      },
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .ui-cover {
    background-color: rgba(0, 0, 0, 1);
    z-index: 19;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    > .bd {
      flex: 1;
      overflow: hidden;
    }
    > .ft {
      font-size: 14px;
      color: #fff;
      display: flex;
      border-top: solid #2f2f2f 1px;
      flex: 0 0 62px;
      button {
        background-color: transparent;
        line-height: 62px;
        color: #fff;
        border: none;
        flex: 1;
        outline: none;
      }
    }
  }
</style>



